/* Убираем отступы */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
}

.btn--reset {
  border: none;
  background-color: transparent;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  height: 100%;
  font-family: "Raleway", "Open Sans";
  font-weight: 500;
  font-size: 17px;
  line-height: 23px;
  line-height: 140%;
  letter-spacing: 0.02em;
}

a {
  color: #000;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
  flex-grow: 1;
}

.footer {
  margin-top: auto;
}

@font-face {
  font-family: "Raleway", serif;
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.header {
  position: fixed;
  width: 100%;
  z-index: 2;
}
.header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 2;
}

.header__inner {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 53px;
  padding-bottom: 44px;
}

.logo {
  position: relative;
  cursor: pointer;
  z-index: 3;
}

.header__nav {
  margin-right: 57px;
}

.nav {
  font-size: 15px;
  z-index: 3;
}

.nav__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 45px;
  font-weight: 600;
  padding: 5px;
}

.nav__link {
  transition: all 0.2s ease-in-out;
}
.nav__link:hover {
  color: #509498;
  transition: all 0.2s ease-in-out;
}

.action {
  display: flex;
  z-index: 3;
}

.action__phone {
  position: relative;
  font-size: 15px;
  letter-spacing: 0.021em;
}
.action__phone:hover {
  color: #509498;
  transition: all 0.2s ease-in-out;
}
.action__phone::before {
  content: "";
  position: absolute;
  top: 0;
  left: -35px;
  width: 27px;
  height: 27px;
  background-image: url(../assets/phone.svg);
  transition: all 0.2s ease-in-out;
}
.action__phone:hover::before {
  content: "";
  position: absolute;
  top: 0;
  left: -35px;
  width: 27px;
  height: 27px;
  background-image: url(../assets/active-phone.svg);
  transition: all 0.2s ease-in-out;
}

.cart {
  position: relative;
  padding-left: 65px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  z-index: 1;
}
.cart:hover {
  transform: scale(1.025);
  transition: all 0.2s ease-in-out;
}

.burger {
  display: none;
  z-index: 4;
}

.hero {
  padding-top: 122px;
  margin-bottom: 180px;
}

.hero__inner {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.hero-slider__left-inner {
  text-align: right;
  padding-top: 145px;
}

.hero-slider__left {
  overflow: hidden;
  max-width: 570px;
}

.hero__title {
  margin-top: 0;
  font-size: 55px;
  line-height: 110%;
  text-align: left;
  margin-bottom: 45px;
}

.hero__text {
  font-weight: 400;
  font-size: 20px;
  text-align: right;
  max-width: 385px;
  line-height: 140%;
  margin-bottom: 74px;
}

.hero__btn {
  position: relative !important;
  margin-right: 185px;
  transition: all 0.2s ease-in-out;
}

.hero__btn--arrow {
  position: absolute;
  top: -24px;
  left: 75px;
  width: 68px;
  height: 68px;
  background-color: rgba(110, 156, 159, 0.1);
  background-image: url(../assets/hero-btn-arrow.svg);
  background-repeat: no-repeat;
  background-position: center;
  z-index: 100;
}

.hero-slider__right {
  overflow: hidden;
}

.hero-slide__inner {
  width: 410px;
  height: 646px;
}

.hero__img {
  width: 80%;
  height: 100%;
  object-fit: cover;
  margin-left: auto;
}

.hero__img--bottom {
  position: absolute;
  right: 350px;
  bottom: -50px;
  width: 190px;
  height: 318px;
  z-index: 1;
}

.hero__pagination {
  text-align: centers;
  margin: 0 auto;
  padding-top: 135px;
  padding-right: 182px;
}

.swiper-pagination-bullet {
  width: 30px;
  height: 4px;
  border-radius: 0;
  background: #6d5a5a;
}

.swiper-pagination-bullet-active {
  background-color: #000;
}

.new-coll {
  margin-bottom: 151px;
}

.new-coll-main__title {
  margin-bottom: 92px;
  text-align: left;
}

.new-coll__inner {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0 30px;
  margin-bottom: 86px;
}

.card-img__inner {
  position: relative;
  max-width: 350px;
  max-height: 478px;
  margin-bottom: 26px;
  transition: all 0.2s ease-in-out;
}
.card-img__inner:hover::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(110, 156, 159, 0.65);
  transition: all 0.2s ease-in-out;
}

.card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card__title {
  text-align: center;
  margin-bottom: 7px;
}

.card__price--old {
  color: #9c9c9c;
  margin-right: 10px;
  font-size: 15px;
  letter-spacing: 0.02em;
  text-decoration: line-through;
}

.card__price {
  text-align: center;
  color: #998e78;
  font-size: 15px;
  letter-spacing: 0.02em;
}

.new-coll__btn-inner {
  width: 100%;
  text-align: center;
}

.important {
  margin-bottom: 130px;
}

.important__title {
  margin-bottom: 92px;
}

.important__inner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 0 30px;
}

.important__card {
  max-width: 310px;
  margin-bottom: 30px;
}

.important__img {
  margin-bottom: 43px;
  line-height: 0.021em;
}

.important-card__title {
  margin-bottom: 30px;
}

.footer__inner {
  height: 100px;
  width: 100%;
  background-color: #f1eadc;
  padding-top: 40px;
}

.burger {
  position: relative;
  width: 30px;
  height: 24px;
  cursor: pointer;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  background-color: black;
  transition: 0.2s ease-in-out;
}
.burger::before {
  top: 0;
}
.burger::after {
  bottom: 0;
}

.burger__line {
  position: absolute;
  width: 75%;
  height: 2px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: black;
}

.burger .line--hide {
  opacity: 0;
  transition: 0.1s ease-in-out;
}

.burger--transform::before {
  top: 50%;
  transform: rotate(45deg);
}

.burger--transform::after {
  top: 50%;
  transform: rotate(-45deg);
}

.--hide {
  display: none;
}

.--visible {
  display: block;
}

body.no-scroll {
  overflow: hidden;
}

.title--main {
  font-weight: 500;
  font-size: 40px;
  line-height: 110%;
  letter-spacing: 0.02em;
}

.title--second {
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
  letter-spacing: 0.02em;
}

.title--middle {
  font-weight: 500;
  font-size: 25px;
  line-height: 140%;
  letter-spacing: 0.02em;
}

.btn--full {
  padding: 24px 49px;
  background-color: #6e9c9f;
  color: #fff;
  transition: all 0.2s ease-in-out;
}
.btn--full:hover {
  background-color: #509498;
}

.btn--transparent {
  border: 1px solid #6e9c9f;
  padding: 22px 50px;
  font-weight: 400;
  font-size: 17px;
  line-height: 139%;
  letter-spacing: 0.02em;
  text-align: center;
  color: #6e9c9f;
  transition: all 0.2s ease-in-out;
}
.btn--transparent:hover {
  background-color: #509498;
  transition: all 0.2s ease-in-out;
  color: #fff;
}

@media (max-width: 991px) {
  .header__inner {
    padding: 20px 0;
  }

  .action__phone {
    display: none;
  }

  .cart {
    padding: 0;
  }
}
@media (max-width: 767px) {
  .header__inner {
    padding: 20px 0;
  }

  .nav {
    position: fixed;
    top: -150%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(110, 156, 159, 0.95);
    z-index: 1;
    transition: all 0.2s ease-in-out;
  }

  .nav.nav--hide {
    top: 0;
    transition: all 0.2s ease-in-out;
  }

  .nav__list {
    position: absolute;
    left: 50%;
    top: 33.33%;
    width: 100%;
    height: 100%;
    transform: translateX(-50%);
    display: block;
    text-align: center;
    z-index: 1;
    overflow: auto;
  }

  .nav__item {
    margin-bottom: 35px;
  }

  .nav__link {
    font-size: 35px;
  }
  .nav__link:hover {
    color: #fff;
  }

  .burger {
    display: block;
  }
}
@media (max-width: 320px) {
  .logo {
    width: 90%;
    margin: 0 auto;
  }
}
