@media (max-width: 991px) {
  .header__inner {
    padding: 20px 0;
  }

  .action__phone {
    display: none;
  }

  .cart {
    padding: 0;
  }
}
@media (max-width: 767px) {
  .header__inner {
    padding: 20px 0;
  }

  .nav {
    position: fixed;
    top: -150%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(110, 156, 159, 0.95);
    z-index: 1;
    transition: all 0.2s ease-in-out;
  }

  .nav.nav--hide {
    top: 0;
    transition: all 0.2s ease-in-out;
  }

  .nav__list {
    position: absolute;
    left: 50%;
    top: 33.33%;
    width: 100%;
    height: 100%;
    transform: translateX(-50%);
    display: block;
    text-align: center;
    z-index: 1;
    overflow: auto;
  }

  .nav__item {
    margin-bottom: 35px;
  }

  .nav__link {
    font-size: 35px;
  }
  .nav__link:hover {
    color: #fff;
  }

  .burger {
    display: block;
  }
}
