.burger {
  position: relative;
  width: 30px;
  height: 24px;
  cursor: pointer;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  background-color: black;
  transition: 0.2s ease-in-out;
}
.burger::before {
  top: 0;
}
.burger::after {
  bottom: 0;
}

.burger__line {
  position: absolute;
  width: 75%;
  height: 2px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: black;
}

.burger .line--hide {
  opacity: 0;
  transition: 0.1s ease-in-out;
}

.burger--transform::before {
  top: 50%;
  transform: rotate(45deg);
}

.burger--transform::after {
  top: 50%;
  transform: rotate(-45deg);
}

.--hide {
  display: none;
}

.--visible {
  display: block;
}

body.no-scroll {
  overflow: hidden;
}

.title--main {
  font-weight: 500;
  font-size: 40px;
  line-height: 110%;
  letter-spacing: 0.02em;
}

.title--second {
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
  letter-spacing: 0.02em;
}

.title--middle {
  font-weight: 500;
  font-size: 25px;
  line-height: 140%;
  letter-spacing: 0.02em;
}

.btn--full {
  padding: 24px 49px;
  background-color: #6e9c9f;
  color: #fff;
  transition: all 0.2s ease-in-out;
}
.btn--full:hover {
  background-color: #509498;
}

.btn--transparent {
  border: 1px solid #6e9c9f;
  padding: 22px 50px;
  font-weight: 400;
  font-size: 17px;
  line-height: 139%;
  letter-spacing: 0.02em;
  text-align: center;
  color: #6e9c9f;
  transition: all 0.2s ease-in-out;
}
.btn--transparent:hover {
  background-color: #509498;
  transition: all 0.2s ease-in-out;
  color: #fff;
}